.wrapper {
  position: relative;
  display: flex;
  flex-flow: column;
  overflow: hidden;
  height: 100vh;
  width: 100%;
  padding: 0;
}

.room__link {
  margin: 0.5rem;
  font-size: x-small;
}

.container {
  position: relative;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 90vh;
  overflow: hidden;
}

.video {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 0;
  background-color: rgba(128, 128, 128, 0.05);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
}

.actions {
  position: relative;
  display: flex;
  height: 10vh;
  width: 100%;
  background-image: rgba(128, 128, 128, 0.05);
}